.header {
  width: 100%;
  font-size: 2rem;
  color: white;
  margin-bottom: 10px;
  text-shadow: 0 0.5px 0 #ccc, 
  0 2px 0 #c9c9c9,
  0 3px 0 #bbb,
  0 4px 0 #b9b9b9,
  0 5px 0 #aaa,
  0 3px 1px rgba(0,0,0,.1),
  0 0 2px rgba(0,0,0,.1),
  0 1px 3px rgba(0,0,0,.3),
  0 3px 5px rgba(0,0,0,.2),
  0 5px 10px rgba(0,0,0,.25),
  0 10px 10px rgba(0,0,0,.2),
  0 5px 5px rgba(0,0,0,.15);
  opacity: 1.0;
}

.title {
  float: left;
}

.scores {
  float: right;
}



